@import url(https://fonts.googleapis.com/css2?family=Recursive:wght@600;700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap);
.settings-item {
    margin-left: auto;
    margin-right: auto;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
}

.settings-textfield {
    max-width: 15rem;
}

.settings-switch {

}
.mobile-menu {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
}

.mobile-item {
    color: #fff;
    font-size: 150%;
    background: rgba(0, 0, 0, 0);
    border: none;
    height: 3.6rem;
    width: 15rem;
}
.mobile-item:hover {
    background: rgba(255, 255, 255, 0.3);
}
#answer-sheet {
    margin-top: 1rem;
}

.choices {
    min-height: 10rem;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 4rem;
    margin-bottom: 6rem;
}

.choice {
    width: 10rem;
    height: 4rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
}

#finish-btn {
    width: 6rem;
    color: #fff;
}
.slider {
    min-width: 9rem;
    max-width: 50rem;
    margin: 0 1.5rem;
}

.nav-btn {
    height: 2rem;
    width: 3rem;
    padding: 0;
    margin: 0 0rem;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background: #eee;
    border: solid #aaa 1px;
    border-radius: 5px;
}
.nav-btn:hover {
    cursor: pointer;
    background: #f5f5f5;
    box-shadow: #aaaabb 0 0 2px;
}
.nav-btn:active {
    background: #fefefe;
}
#ans-input {
    padding: 0.5rem;
    margin: 0;
    margin-left: 20px; margin-right: 20px;
    overflow: auto;
    font-size: 100%;
    border-radius: 0.25em;
    border: solid #ddd 2px;
    text-align: center;
    resize: none;
}

#result-table {
    border: solid #eee 2px;
    border-radius: 1.5rem;
    box-shadow: #ddd 0 0 10px;
}
/* FONTS */

* {
    margin: 0;
    padding: 0;
    font-family: 'Rubik', 'Roboto', 'Arial';
    outline: none;
}

a.a-btn {
    display: block;
    color: blue;
    text-decoration: underline;
}
a.a-btn:hover {
    color: purple;
    cursor: pointer;
}

.row {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

#root {
    min-width: 20rem;
}

.control-btn {
    background: none;
    border: none;
    color: #555;
}
.control-btn:hover {
    color: #999;
    cursor: pointer;
}

.section {
    max-width: 40rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    margin: auto;
    text-align: left;
}

.base-btn {
    border: none;
    border-radius: 0;
    box-shadow: 0;
    background: none;
    padding: 0;
    margin: 0;
    text-align: center;
}

.icon-btn {
    color: #555;
}
.icon-btn:hover {
    color: #999;
}
/*----------LOGIN----------*/
#google-signin-btn {
    background-image: url(/mctest/static/media/google_logo.45446013.svg);
    background-color: #4285F4;
    color: #ffffff;
    font-family: 'Roboto', sans-serif;
}
#google-signin-btn:hover {
    background-color: #1f65d6;
}

#facebook-signin-btn {
    background-image: url(data:image/png;base64,);
    background-color: #1877F2;
    color: #ffffff;
    font-family: 'Roboto', sans-serif;
}
#facebook-signin-btn:hover {
    background-color: #1455aa;
}

#github-signin-btn {
    background-image: url(data:image/png;base64,);
    background-color: #000000;
    color: #ffffff;
    font-family: 'Roboto', sans-serif;
}
#github-signin-btn:hover {
    background-color: #2f2f2f;
}

.signin-btn {
    width: 240px;
    height: 36px;
    padding-left: 36px;
    background-position: 0px 0px;
    background-repeat: no-repeat;
    background-size: auto 100%;
    cursor: pointer;
    margin-bottom: 1vh;
    border: none;
    border-radius: 2px;
}

#mytests-table {
    border: solid #eee 2px;
    border-radius: 1.5rem;
    box-shadow: #ddd 0 0 10px;
    min-height: 50vh;
}

a.test-link {
    display: block;
    color: #000;
    text-decoration: underline;
}
a.test-link:hover {
    color: #000099;
    cursor: pointer;
}
